.Button {
    position: fixed;
    top: 0;
    font-size: 60px;
    color: white;
    opacity: 0.1;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(12, 15, 15, 0.01)), to(rgba(82, 46, 71, 0.01)));
    background: -webkit-linear-gradient(top, rgba(12, 15, 15, 0.01), rgba(82, 46, 71, 0.01));
    background: -o-linear-gradient(top, rgba(12, 15, 15, 0.01), rgba(82, 46, 71, 0.01));
    background: linear-gradient(to bottom, rgba(12, 15, 15, 0.01), rgba(82, 46, 71, 0.01));
    border: 0;
    padding: 0;
}

.Button:hover {
    opacity: 1;
}

.Button:focus {
    outline: 0;
}

.Button_Left {
    left: 0;
    text-align: left;
    width: 50%;
    height: 100vh;
}

.Button_Exit {
    width: 100%;
    height: 5%;
    z-index: 10;
}

.Button_Right {
    right: 0;
    text-align: right;
    width: 50%;
    height: 100vh;
}



