.Overlay {
    width: 100%;
    height: 100vh;
    left: 0;
    top: 0;
    background-color: white;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(12, 15, 15, 0.8)), to(rgba(10, 6, 23, 0.89)));
    background: -webkit-linear-gradient(top, rgba(12, 15, 15, 0.8), rgba(10, 6, 23, 0.89));
    background: -o-linear-gradient(top, rgba(12, 15, 15, 0.8), rgba(10, 6, 23, 0.89));
    background: linear-gradient(to bottom, rgba(12, 15, 15, 0.8), rgba(10, 6, 23, 0.89));
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: fixed;
}



